<template>
    <div>
        <van-tabs v-model="active" animated   @change="changeTab" sticky='true' offset-top='1.44rem' >
			<van-tab v-for="(item,index) in tabList"  :key='item'>
					<template #title> 
						{{item.name}}
						<span v-if="item.icon">
							<i class="angle_top"></i>
    						<i class="angle_bottom"></i>
						</span> 			
					</template>
					<!--...-->
					<div style="height:calc(100vh - 2.77333rem );background-color:#fff;margin-top: 1.44rem; margin-bottom:1.3333rem;positon:relative" v-if="item.name!=''">
						<!--mescroll-->	
						<mescroll-vue :ref="'mescroll-'+(index+1)" :up="mescrollUp" @init="mescrollInit(index,arguments)">
							<water-fall :list='list' :id='waterFall+index'></water-fall>
						</mescroll-vue>
					</div>
			</van-tab>
		</van-tabs>
    </div>
</template>
<script>
import waterFall from "@/components/waterFall/waterFall.vue"
import MescrollVue from 'mescroll.js/mescroll.vue'
import allList from '@/views/goods/api/data'  // 
export default {
    name:'GoodsMain',
    props:{},
    components:{
        waterFall,
        MescrollVue, 
    },
    data(){
        return{
            /* tabs */
            tabList:[
                {
                    name:'',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'',
                    mescroll:null,
                    icon:true,
                    list:[]
                },
                {
                    name:'',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'',
                    mescroll:null,
                    list:[]
                },
                {
                    name:'',
                },
            ],
            fontTabIndex:0,
            list:[],
            mescrollDown:{}, //. (,mescrollDown)
            mescrollUp: { // .
                callback: this.upCallback, // ,;  callback: function(page, mescroll) { }
                //,.
                page: {
                    num: 0, // 0,1; callback(page)1
                    size: 10 //,10
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
                noMoreSize: 5, 
                            // ,5;
                            // (),
                            // 
                toTop: {
                    //
                    src: "./static/mescroll/mescroll-totop.png", //,null,
                    offset: 800, //1000px
                    warpClass:" backTop "
                },
                empty: {
                // ,; warpId;
                    warpId: 'waterFall', // id;
                    icon: './static/mescroll/mescroll-empty.png', // ,
                    tip: '~', // 
                    // btntext: ' >', // ,""
                    // btnClick () { // ,null
                    // 	alert(',')
                    // }
                },
                lazyLoad: {
                    use: true // ,false
                }
            },
        }
    },
    beforeRouteLeave (to, from, next) { // isBounce,beforeRouteLeave
        // ,isBounce
        Object.keys(this.$refs).forEach(key => {
            this.$refs[key][0] && this.$refs[key][0].beforeRouteLeave();
        })
        next()
    },
    methods:{
        changeTab(tabIndex){
				const {fontTabIndex} = this
				this.tabList[fontTabIndex].mescroll && this.tabList[fontTabIndex].mescroll.hideTopBtn(); // 
				this.fontTabIndex = tabIndex  //tabIndex
				if(	this.tabList[tabIndex].name=='') return
				// 
				setTimeout(() => {
					let curScrollTop = this.tabList[tabIndex].mescroll.getScrollTop()
					if (curScrollTop >= this.tabList[tabIndex].mescroll.optUp.toTop.offset) {
						this.tabList[tabIndex].mescroll.showTopBtn()
					} else {
						this.tabList[tabIndex].mescroll.hideTopBtn()
					}
				}, 30)
			},
			// mescroll
			mescrollInit (tabIndex, arg) {
				this.tabList[tabIndex].mescroll = arg[0] 
				this.tabList[tabIndex].mescroll.tabIndex = tabIndex 
				this.fontTabIndex = tabIndex //tabIndex
			},
			 // mescroll
			getMescrollUp (tabIndex) {
				let emptyWarpId = 'dataList' + tabIndex;
				return {
					auto: false,
					callback: this.upCallback, // ,;  callback: function (page) { upCallback(page); }
					noMoreSize: 4, // ,;(),; 5
					empty: {
					warpId: emptyWarpId, // id;
					icon: 'https://www.mescroll.com/img/mescroll-empty.png', // ,null
					tip: '~', // 
					btntext: ' >', // ,""
					btnClick: function () { // ,null
						alert(',')
					}
					},
					toTop: { // 
					src: 'https://www.mescroll.com/img/mescroll-totop.png' // ,null (,)
					}
				}
			},
			//  page = {num:1, size:10}; num: ,1; size:,10
			upCallback (page, mescroll) {
			// 
			this.getListDataFromNet(page.num, page.size, (arr) => {
					// 
					if (page.num === 1) this.list = []
					// 
					this.list = this.list.concat(arr)
					// ,
					this.$nextTick(() => {
					mescroll.endSuccess(arr.length)
					})
				}, () => {
					// ,
					mescroll.endErr()
				})
			},

            /* api */
			/* 
			,: https://www.mescroll.com/api.html#tagUpCallback
			getListDataFromNet,,
			,.
			* */
			getListDataFromNet ( pageNum, pageSize, successCallback, errorCallback) {
				// ,
				setTimeout(() => {
					// axios.get("xxxxxx", {
					//   params: {
					//     num: page.num, //
					//     size: page.size //
					//   }
					// }).then((response)=> {
					var listData = []
					listData = allList.slice((pageNum-1)*pageSize,pageNum*pageSize)
				
					// 
					successCallback(listData)
					// }).catch((e)=> {
					//   //,;
					//   errorCallback&&errorCallback()
					// })
				}, 1000)
            },
            
            
    }    
}
</script>
<style lang="scss" scoped>
    .angle_top {
      content: '';
      width: 0;
      height: 0;
      display: block;
      border-style: solid;
      border-width: 0 5px 5px;
      border-color: transparent transparent #5e5e5e;
      position: absolute;
      transform: rotate(180deg);
      bottom: 15px;
      right: 10px;
    }

    .angle_bottom {
      content: '';
      width: 0;
      height: 0;
      display: block;
      border-style: solid;
      border-width: 0 5px 5px;
      border-color: transparent transparent #5e5e5e;
      position: absolute;
      top: 17px;
      right: 10px;
	}
</style>


